import {HttpClient, HttpClientModule} from '@angular/common/http';
import {NgModule} from '@angular/core';
import {BrowserModule} from '@angular/platform-browser';
import {TranslateLoader, TranslateModule} from '@ngx-translate/core';

import {AppRoutingModule} from './app-routing.module';
import {AppComponent} from './app.component';
import {CompanyComponent} from './company/company.component';
import {HomeComponent} from './home/home.component';
import {PageNotFoundComponent} from './page-not-found/page-not-found.component';
import {SharedModule} from './shared/shared.module';

import {Injectable, Injector} from '@angular/core';
import {Observable} from 'rxjs/Observable';

/**
 * here, we use ngx-translate as service
 */
@Injectable()
export class LanguageLoader implements TranslateLoader {
  /**
   * This is the constructor of TranslateLoader
   */
  constructor(private inej: Injector) {}

  /**
   * We redefine the getTranslation method of ngs-translate with our local api and using HttpClient
   */
  public getTranslation(lang: string): Observable<any> {
    const http = this.inej.get(HttpClient);
    return http.get(`./assets/api/${lang}.json`);
  }
}

/**
 * Our rout module
 */
@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    CompanyComponent,
    PageNotFoundComponent
  ],
  imports: [
    HttpClientModule,
    BrowserModule,
    TranslateModule.forRoot({
      loader: {provide: TranslateLoader, useClass: LanguageLoader}
    }),
    SharedModule,
    AppRoutingModule
  ],
  providers: [],
  // tslint:disable-next-line:object-literal-sort-keys
  bootstrap: [AppComponent]
})
export class AppModule { }
