import {Component, OnInit} from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {TranslateService} from '@ngx-translate/core';
import {IPageContain} from '../page-contain';
import {Grid} from '../shared/utility/grid/grid';
import {Sort} from '../shared/utility/sort/sort';

/**
 * company papge
 */
@Component({
  selector: 'app-company',
  styleUrls: ['./company.component.css'],
  templateUrl: './company.component.html'
})

export class CompanyComponent implements OnInit {
  /**
   * Contain all the category of our data
   */
  public categoryArray = [];
  /**
   * Contain the data to display on the view
   */
  // tslint:disable:object-literal-sort-keys
  // tslint:disable:max-line-length
  public contain: IPageContain[] = [];

  // Contain the language that is actually apply
  public lang: string;

  /**
   * @ignore
   */
  constructor(private dataService: TranslateService, private route: ActivatedRoute) {
    //
  }

  /**
   * Initialize some variable
   */
  public ngOnInit(): void {
    /**
     * this statement retrieves the language parameter in the URL
     */
    this.route.paramMap.subscribe(pmap => this.lang = pmap.get('lang'));

    this.dataService.getTranslation('pageContain_' + this.lang).subscribe(
      data => {
        this.contain = data.filter(value => value.id === 'company');
        this.contain[0].category = this.contain[0].category.sort(Sort.arraySort);
        this.contain[0].category.forEach(element => {
          // I sort the article from priority 1 to last priority
          element.article = element.article.sort(Sort.arraySort);
          this.categoryArray.push(element);
        });
      }
    );
  }

  /**
   * compute the size of an element and return the bootstrap class that match the size
   * @param article the element we want to compute the size on the html view
   * @returns the size of the element on the view
   */
  public gridClass(article: any[]): string {
    let activeStatut = 0;
    article.forEach(element => {
      if (element.statut === 1) {
        activeStatut += 1;
      }
    });
    return Grid.gridSystem_sm(activeStatut);
  }

  /**
   * apply the border-bottom style on an element
   * @param article will allow to verify that the size of the element we want to apply the style
   * is greater than 1
   */
  public styleClass(article: any[]): {} {
    let activeStatut = 0;
    if (article.length > 1) {
      article.forEach(element => {
        if (element.statut === 1) {
          activeStatut += 1;
        }
      });
    }
    return (activeStatut > 1) ? {'border-bottom': '1px solid rgb(230, 214, 214)'} : '';
  }

  /**
   * @ignore
   */
  public trackByFn(index, item) {
    return index; // or item.id
  }
}
